import { Typography, Button, TextField, FormControlLabel, FormControl, FormHelperText, Radio, FormLabel, RadioGroup } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory, useParams } from 'react-router-dom';
import { useRef, useState } from 'react';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { format} from 'date-fns';




function MuokkaaKirjausta(props) {

    const history = useHistory();
    const { id } = useParams();
    const [pvmaika, setPvmaika] = useState(new Date());
    const tekstikentta = useRef();
    const maara = useRef();
    const [virheilmoitus, setVirheilmoitus] = useState({});
    const lomaketiedot = useRef({});
    const muokattavaKirjaus = props.kirjaukset.filter((kirjaus) => {
        return (kirjaus.id === id);
    })[0];
   
    const kasitteleLomake = (e) => {
        
        e.preventDefault();
        let virheet = {};
        if (!tekstikentta.current.value) {
          virheet = {...virheet, nimi: "Kirjauksen nimi puuttuu"}
        }
       if
       (!pvmaika) {
           virheet = {...virheet, kirjausAika: "Valitse päivämäärä"}
         }

         if (!lomaketiedot.current.laji) {

          virheet = {...virheet, laji : "Valitse meno/tulolaji"}
          
        } 

        if (!maara) {

          virheet = {...virheet, maara : "Syötä summa"}
          
        } 

        if (lomaketiedot.current.laji === "Palkat/etuudet" && maara.current.value < 0) {
  
          virheet = {...virheet, maara : "Syötä tulot positiivisena lukuna"}
          
        } 
  
        if (lomaketiedot.current.laji === "Muu tulo" && maara.current.value < 0) {
  
          virheet = {...virheet, maara :"Syötä tulot positiivisena lukuna"}
          
        }
        
  
        if (lomaketiedot.current.laji === "Asuminen" && maara.current.value > 0) {
  
          virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
          
        } 
  
        if (lomaketiedot.current.laji === "Ruoka ja juoma" && maara.current.value > 0) {
  
          virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
          
        } 
  
        if (lomaketiedot.current.laji === "Liikkuminen" && maara.current.value > 0) {
  
          virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
          
        } 
  
        if (lomaketiedot.current.laji === "Huvit" && maara.current.value > 0) {
  
          virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
          
        } 
  
        
        if (lomaketiedot.current.laji === "Muut" && maara.current.value > 0) {
  
          virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
          
        } 
  
       
         if (Object.entries(virheet).length > 0) {
 
             setVirheilmoitus({...virheet});
       
           } else {
           
        let kirjauksetApu = props.kirjaukset.filter(() => {
            return  (muokattavaKirjaus.id = id,
                muokattavaKirjaus.nimi = tekstikentta.current.value,
                muokattavaKirjaus.maara = maara.current.value,
                muokattavaKirjaus.laji = lomaketiedot.current.laji,
                muokattavaKirjaus.kirjattu = pvmaika.getTime());
        });
    
        props.setKirjaukset([...kirjauksetApu]);
    
        history.push("/");

    

    }}
    const syoteKasittelija = (e) => {

        let arvo = (e.target.type === "checkbox") ? e.target.checked : e.target.value;
    
        lomaketiedot.current[e.target.name] = arvo;
    
      }

    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
        <form onSubmit={kasitteleLomake}>


        <Otsikko tyyli="pieni">Kirjauksen muokkaus</Otsikko>

        <Typography 
         style={{fontSize: 12}}
        >Tee muutokset menoon/tuloon: {muokattavaKirjaus.nimi} {muokattavaKirjaus.maara} €</Typography>
          <Typography
          style={{fontSize: 12}}
        >Kirjauksen laji: {muokattavaKirjaus.laji}</Typography>
          <Typography
           style={{marginBottom : 10, fontSize: 12}}
        >Kirjausaika: {format(muokattavaKirjaus.kirjattu, "d.M.y")}</Typography>

          
            <Otsikko tyyli="pieni">Muokkaa menoa/tuloa</Otsikko>

            <TextField 
                 name = "nimi"
                label="Menon/tulon nimi"
                variant="outlined"
                defaultValue={muokattavaKirjaus.nimi}
                fullWidth={true}
                inputRef={tekstikentta}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.nimi)}
                helperText={virheilmoitus.nimi}
            />


        <TextField     
                name = "maara"
                label="Summa €"
                variant="outlined"
                defaultValue={muokattavaKirjaus.maara}
                fullWidth={true}
                inputRef={maara}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.maara)}
                helperText={virheilmoitus.maara}
            />
              <DatePicker
                name = "kirjattu"
                label="Tapahtumapäivä"
                inputVariant="outlined"
                defaultValue={muokattavaKirjaus.kirjattu}
                fullWidth={true}
                cancelLabel="Peruuta"
                ampm={false}
                value={pvmaika}
                format="d.M.y"
                onChange={setPvmaika}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.kirjausAika)}
                helperText={virheilmoitus.kirjausAika}
            />

            
<FormControl fullWidth style={{marginTop : 10}}>
       <FormLabel>Tulo/menolaji</FormLabel>
        <RadioGroup>
          <FormControlLabel 
            type = "checkbox"
            value="Palkka/etuus" 
            label="Palkka tai etuus"
            control={<Radio 
                        color="primary"
                        name="laji"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="muu tulo" 
            type= "checkbox"
            label="Muu tulo"
            control={<Radio 
                       color="primary"
                        name="laji"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="Asuminen" 
            type = "checkbox"
            label="Asuminen"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
              <FormControlLabel 
            type =  "checkbox"
            value="Liikkuminen" 
            label="Liikkuminen"
            control={<Radio
                        color="primary" 
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
             type = "checkbox"
            value="Ruoka ja juoma" 
            label="Ruoka ja juoma"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
            value="Huvit" 
            type =  "checkbox"
            label="Huvit"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
             type = "checkbox"
            value="Muut" 
            label="Muut"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
        </RadioGroup>
        <FormHelperText error={Boolean(virheilmoitus.laji)}>{virheilmoitus.laji}</FormHelperText>      
      </FormControl>



            <Button 
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{marginBottom : 10}}
            >Tallenna</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
            >Peruuta</Button>
            </form>
        </MuiPickersUtilsProvider>
    )
}

export default MuokkaaKirjausta;