import { Button, IconButton, List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import Otsikko from "./Otsikko";
import { Link } from 'react-router-dom';
import { MuiPickersUtilsProvider} from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { useState, useEffect } from 'react';
import { makeStyles} from '@material-ui/core';
import { format} from 'date-fns';
import isThisMonth from 'date-fns/isThisMonth';

function Kirjauslista(props) {

const [yhteensa, setYhteensa] = useState(0);
const [tulot, setTulot] = useState(0);
const [menot, setMenot] = useState(0);
const [tulotKuluvaKK, setTulotKuluvaKK] = useState(0);
const [menotKuluvaKK, setMenotKuluvaKK] = useState(0);
const [kuluvaKK, setKuluvaKK] = useState(0);
const useStyles = makeStyles({

});
  
  const yhteensaKirjaukset = () => {
    let kirjauksetYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
     kirjauksetYht += Number(props.kirjaukset[i].maara);
    }
    setYhteensa(kirjauksetYht.toFixed(2));
  };


  const kuluvaKuukausi = () => {
    let tamaKK = 0;
   
  
    for (let i = 0; i < props.kirjaukset.length; i++) {
      var result;
      if(result = isThisMonth(new Date(props.kirjaukset[i].kirjattu))){
      tamaKK += Number(props.kirjaukset[i].maara);
    }
    else{tamaKK += 0}
  }

  setKuluvaKK(tamaKK.toFixed(2));
      
  }

  const kuluvaKuukausiMenotTulot = () => {
    let kuluvaKKMenot = 0;
    let kuluvaKKTulot = 0;
   
  
    for (let i = 0; i < props.kirjaukset.length; i++) {
      var result;
      if(result = isThisMonth(new Date(props.kirjaukset[i].kirjattu)) && props.kirjaukset[i].laji===("Palkka/etuus") || props.kirjaukset[i].laji===("Muu tulo")){
      kuluvaKKTulot += Number(props.kirjaukset[i].maara);
    }
    else if (result = isThisMonth(new Date(props.kirjaukset[i].kirjattu))){
      
      kuluvaKKMenot += Number(props.kirjaukset[i].maara);
    }
  }

  setTulotKuluvaKK(kuluvaKKTulot.toFixed(2));
  setMenotKuluvaKK(kuluvaKKMenot.toFixed(2));
      
  }
  const kaikkiTulotMenot = () => {
   
  
    
      var tulotYht = 0;
      var menotYht = 0;
      for (let i = 0; i < props.kirjaukset.length; i++) {
      if(props.kirjaukset[i].laji===("Palkka/etuus") || props.kirjaukset[i].laji===("Muu tulo")){
      tulotYht += Number(props.kirjaukset[i].maara);
    }
    else {menotYht += Number(props.kirjaukset[i].maara);
    };
  }

  setTulot(Number(tulotYht.toFixed(2)));
  setMenot(Number(menotYht.toFixed(2)));
      
  }

 
  useEffect(() => {

    yhteensaKirjaukset(); kuluvaKuukausi(); kaikkiTulotMenot(); kuluvaKuukausiMenotTulot();

  }, [props.kirjaukset]);

  const tyylit = useStyles();
      


    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
        <>

        <Otsikko>Tulo- ja kulukirjaus</Otsikko>

     

        <List>

      
     { props.kirjaukset.sort((a, b) => a.kirjattu - b.kirjattu).map( (kirjaus, id) => {
          return (
                  <ListItem key={id} style={{color: kirjaus.laji==="Palkka/etuus" || kirjaus.laji==="Muu tulo" ? "green" : "red"}} >
                      <ListItemText style={{ width:"15%"}}primary={kirjaus.nimi}/>  
                      <ListItemText style={{ width:"5%"}}primary={kirjaus.maara}  /> <ListItemText style={{ width:"5%"}}primary={"€"}/>
                      <ListItemText style={{ width:"20%"}}primary={kirjaus.laji}/>
                      <ListItemText style={{ width:"20%"}}secondary={format(kirjaus.kirjattu, "d.M.y")}/>
                      <ListItemIcon>
                        <IconButton 
                          edge="end"
                          component={Link}
                          to={`/muokkaa/${kirjaus.id}`} 
                        >
                            <EditIcon />
                        </IconButton>
                        </ListItemIcon>
                      <ListItemIcon>
                        <IconButton 
                          edge="end"
                          component={Link}
                          to={`/poista/${kirjaus.id}`} 
                        >
                            <DeleteIcon />
                        </IconButton>
                        </ListItemIcon>
                  </ListItem>
                );
  
        } ) }
  
        </List>
        <Otsikko tyyli="pieni">
   Tulot: {tulot} €, menot: {menot}, yhteensä : {yhteensa} €
   </Otsikko>
   <Otsikko tyyli="pieni">
    Kuluva kuukausi:   Tulot: {tulotKuluvaKK} €, menot: {menotKuluvaKK}, yhteensä : {kuluvaKK} €
   </Otsikko>
     <Button
           component={Link}
           to="/uusi"
           variant="contained"
           color="primary"
           fullWidth
           size="large"
         >Lisää uusi tulo tai meno</Button>
        </>
        </MuiPickersUtilsProvider>
  
    )
}

export default Kirjauslista