import { Container, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core'; 
import { useState, useEffect } from 'react';





const useStyles = makeStyles({
    sisalto : {
        margin : "auto",
        width : "700px"
    },

    otsikko : {
        marginTop : 10,
        fontSize : 22
    },
    alaotsikko : {
        marginTop : 10,
        fontSize : 18
    },
    kappale : {
        marginTop : 10,
        fontSize : 14,
        widht : 600
    }
});

function Yhteenveto(props) {

const [palkatJaEtuudet, setPalkatJaEtuudet] = useState(0);
const [ruoat, setRuoat] = useState(0);
const [huvit, setHuvit] = useState(0);
const [liikkuminen, setLiikkuminen] = useState(0);
const [asuminen, setAsuminen] = useState(0);
const [muuTulo, setMuuTulo] = useState(0);
const [muu, setMuu] = useState(0);
const [kaikkiYhteensa, setKaikkiYhteensa] = useState(0);




    const tyylit = useStyles();

 


    const kaikkiPalkat = () => {
        let palkatYht = 0;
        for (let i = 0; i < props.kirjaukset.length; i++) {
            if( props.kirjaukset[i].laji==="Palkka/etuus"){
          palkatYht += Number(props.kirjaukset[i].maara);
            }
        
        }
        

        setPalkatJaEtuudet(palkatYht.toFixed(2));

    };


  const kaikkiRuoat = () => {
    let ruoatYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Ruoka ja juoma"){
      ruoatYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setRuoat(ruoatYht.toFixed(2));

};



const kaikkiHuvit = () => {
    let huvitYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Huvit"){
      huvitYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setHuvit(huvitYht.toFixed(2));

};


const kaikkiLiikkuminen = () => {
    let liikkuminenYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Liikkuminen"){
      liikkuminenYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setLiikkuminen(liikkuminenYht.toFixed(2));
};

const kaikkiAsuminen = () => {
    let asuminenYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Asuminen"){
      asuminenYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setAsuminen(asuminenYht.toFixed(2));
};

const kaikkiMuuTulo = () => {
    let muuTuloYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Muu tulo"){
      muuTuloYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setMuuTulo(muuTuloYht.toFixed(2));
};


const kaikkiMuu = () => {
    let muuYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {
        if( props.kirjaukset[i].laji==="Muut"){
      muuYht += Number(props.kirjaukset[i].maara);
        }
    
    }
    

    setMuu(muuYht.toFixed(2));
};



const kaikkiKirjauksetYhteensa = () => {
    let kaikkiYht = 0;
    for (let i = 0; i < props.kirjaukset.length; i++) {

      kaikkiYht += Number(props.kirjaukset[i].maara);  
    }
    

    setKaikkiYhteensa(kaikkiYht.toFixed(2));
};

useEffect(() => {

 kaikkiPalkat(); kaikkiRuoat(); kaikkiHuvit(); kaikkiLiikkuminen(); 
kaikkiAsuminen(); kaikkiMuuTulo(); kaikkiMuu(); kaikkiKirjauksetYhteensa();

}, [props.kirjaukset]);


    return (
        <Container className={tyylit.sisalto}>

            <Typography className={tyylit.otsikko}>
               Yhteenveto tuloista ja menoista
            </Typography>


            <Typography className={tyylit.alaotsikko}>
          
            </Typography>
            <Typography className={tyylit.kappale}>
                <h3> Tulo- ja menolajit </h3>
                <p> Palkat ja etuudet: {palkatJaEtuudet} € </p>
                <p> Muut tulot: {muuTulo} €</p>
                <p> Liikkuminen: {liikkuminen} €</p>
                <p> Asuminen: {asuminen} €</p>
                <p> Ruoat: {ruoat} €</p>
                <p> Huvit: {huvit} €</p>
                <p> Muut menot: {muu} €</p>

                <p style={{fontWeight:"bold"}}> Yhteensä: {kaikkiYhteensa} € </p>
            </Typography>

        </Container>
    )
}

export default Yhteenveto