import { useState, useEffect } from 'react';
import { Container } from '@material-ui/core';
import Valikko from './components/Valikko';
import UusiKirjaus from './components/UusiKirjaus';
import Kirjauslista from './components/Kirjauslista';
import Yhteenveto from './components/Yhteenveto';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import PoistaKirjaus from './components/PoistaKirjaus';
import MuokkaaKirjausta from './components/MuokkaaKirjausta';

function App() {

  const [kirjaukset, setKirjaukset] = useState([]);

  const tallennaKirjauslista = () => {

    localStorage.setItem("kirjauslista", JSON.stringify(kirjaukset));

  }

  const avaaKirjauslista = () => {

    if (localStorage.getItem("kirjauslista")) {

      setKirjaukset(JSON.parse(localStorage.getItem("kirjauslista")));

    } else {

      setKirjaukset([]);
    }

  }

  useEffect(() => {

    avaaKirjauslista();

  }, []);

  useEffect(() => {

    tallennaKirjauslista();

  }, [kirjaukset]); 
                              

  return (
    <Router>
      <Container>
        <Valikko/>

        <Route path="/" exact>
          <Kirjauslista kirjaukset={kirjaukset} setKirjaukset={setKirjaukset} tallennaKirjauslista={tallennaKirjauslista}/>
        </Route>

        <Route path="/uusi">
          <UusiKirjaus kirjaukset={kirjaukset} setKirjaukset={setKirjaukset}/>
        </Route>

        <Route path="/poista/:id">
          <PoistaKirjaus kirjaukset={kirjaukset} setKirjaukset={setKirjaukset}/>
        </Route>

        <Route path="/muokkaa/:id">
          <MuokkaaKirjausta kirjaukset={kirjaukset} setKirjaukset={setKirjaukset}/>
        </Route>

        <Route path="/yhteenveto">
          <Yhteenveto kirjaukset={kirjaukset} setKirjaukset={setKirjaukset}/>
        </Route>

      </Container>
    </Router>
  );
}

export default App;