import { useRef, useState } from 'react';
import { Button, TextField, FormControlLabel, FormControl, FormHelperText, Radio, FormLabel, RadioGroup } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory } from 'react-router-dom';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';

function UusiKirjaus(props) {

    const [pvmaika, setPvmaika] = useState(new Date());
    const tekstikentta = useRef();
    const maara = useRef(Number(0));
    const history = useHistory();
    const [virheilmoitus, setVirheilmoitus] = useState({});
    const lomaketiedot = useRef({});
   
    


    const kasitteleLomake = (e) => {

        e.preventDefault();
       let virheet = {};

       if (!tekstikentta.current.value) {
        virheet = {...virheet, nimi: "Kirjauksen nimi puuttuu"}
      }
    
      if (!lomaketiedot.current.laji) {

        virheet = {...virheet, laji : "Valitse meno/tulolaji"}
        
      } 

      if (!maara) {

        virheet = {...virheet, maara : "Syötä summa"}
        
      } 
      
      if (lomaketiedot.current.laji === "Palkat/etuudet" && maara.current.value < 0) {
  
        virheet = {...virheet, maara : "Syötä tulot positiivisena lukuna"}
        
      } 

      if (lomaketiedot.current.laji === "Muu tulo" && maara.current.value < 0) {

        virheet = {...virheet, maara :"Syötä tulot positiivisena lukuna"}
        
      }

      if (lomaketiedot.current.laji === "Asuminen" && maara.current.value > 0) {

        virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
        
      } 

      if (lomaketiedot.current.laji === "Ruoka ja juoma" && maara.current.value > 0) {

        virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
        
      } 

      if (lomaketiedot.current.laji === "Liikkuminen" && maara.current.value > 0) {

        virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
        
      } 

      if (lomaketiedot.current.laji === "Huvit" && maara.current.value > 0) {

        virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
        
      } 

      
      if (lomaketiedot.current.laji === "Muut" && maara.current.value > 0) {

        virheet = {...virheet, maara : "Syötä menot negatiiviisena lukuna"}
        
      } 

     
      
        if (Object.entries(virheet).length > 0) {

            setVirheilmoitus({...virheet});
      
          } else {
    
         
        let uusiKirjausApu = {
                                id : uuid(),
                                nimi : tekstikentta.current.value,
                                maara : maara.current.value.replace(/,/g, '.'),
                                laji : lomaketiedot.current.laji,
                                kirjattu : pvmaika.getTime()
                            
                             }
    
        props.setKirjaukset([...props.kirjaukset, uusiKirjausApu]);
        
    
        history.push("/");

                            }
    }

    const syoteKasittelija = (e) => {

        let arvo = (e.target.type === "checkbox") ? e.target.checked : e.target.value;
    
        lomaketiedot.current[e.target.name] = arvo;
    
      }
    


    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <form onSubmit={kasitteleLomake}>

            <Otsikko tyyli="pieni">Lisää uusi kirjaus</Otsikko>
            <p>Syötä menot positiivisena lukuna ja menot negatiivisena</p>

            <TextField 
                label="Menon/tulon nimi"
                name= "nimi"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.nimi)}
                helperText={virheilmoitus.nimi}
            />

            <TextField 
                label="Summa €"
                name="maara"
                type="decimal"
                variant="outlined"
                fullWidth={true}
                inputRef={maara}
                style={{marginBottom : 10}}
                error={Boolean(virheilmoitus.maara)}
                helperText={virheilmoitus.maara}
            />

<FormControl fullWidth style={{marginTop : 10}}>
       <FormLabel>Tulo/menolaji</FormLabel>
        <RadioGroup>
          <FormControlLabel 
            type = "checkbox"
            value="Palkka/etuus" 
            label="Palkka tai etuus"
            control={<Radio 
                        color="primary"
                        name="laji"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="Muu tulo" 
            type= "checkbox"
            label="Muu tulo"
            control={<Radio 
                       color="primary"
                        name="laji"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="Asuminen" 
            type = "checkbox"
            label="Asuminen"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
              <FormControlLabel 
            type =  "checkbox"
            value="Liikkuminen" 
            label="Liikkuminen"
            control={<Radio
                        color="primary" 
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
             type = "checkbox"
            value="Ruoka ja juoma" 
            label="Ruoka ja juoma"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
            value="Huvit" 
            type =  "checkbox"
            label="Huvit"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
            <FormControlLabel 
             type = "checkbox"
            value="Muut" 
            label="Muut"
            control={<Radio 
                        color="primary"
                        name="laji" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
        </RadioGroup>
        <FormHelperText error={Boolean(virheilmoitus.laji)}>{virheilmoitus.laji}</FormHelperText>      
      </FormControl>


            <Button 
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{marginBottom : 10}}
            >Lisää uusi kirjaus</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
            >Peruuta</Button>

            </form>
        </MuiPickersUtilsProvider>
    )
}

export default UusiKirjaus;