import {useState} from 'react';
import {AppBar ,IconButton,Toolbar, CssBaseline, Drawer, List, ListItem, ListItemText, ListItemIcon} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import EuroIcon from '@material-ui/icons/Euro';
import {Link} from 'react-router-dom';

function Valikko() {

    const [naytaValikko, setNaytaValikko] = useState(false);

    return (
        <CssBaseline>
            <AppBar position="static">
                <Toolbar>
                    <IconButton 
                        edge="start" 
                        color="inherit"
                        onClick={ () => { setNaytaValikko(true) } }
                    >
                        <MenuIcon />
                    </IconButton>
                    <Drawer
                        open={naytaValikko}
                        onClose={ () => { setNaytaValikko(false) } }
                    >
                        <List style={{
                                width : 220,
                                marginTop : 50 
                            }}
                            onClick={ () => { setNaytaValikko(false) } }
                        >
                            <ListItem>
                                <h2>Tulo- ja menokirjaus</h2>
                                
                            </ListItem>
                            <ListItem button component={Link} to="/">
                                <ListItemIcon>
                                    <HomeIcon />
                                </ListItemIcon>
                                <ListItemText primary="Tulot ja menot"/>
                            </ListItem>

                            <ListItem button component={Link} to="/yhteenveto">
                                <ListItemIcon>
                                    <EuroIcon />
                                </ListItemIcon>
                                <ListItemText primary="Yhteenveto"/>
                            </ListItem>
                          

                        </List>
                    </Drawer>
                </Toolbar>
            </AppBar>
        </CssBaseline>
    )
}

export default Valikko;