import { Button, Typography } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory, useParams } from 'react-router-dom';

function PoistaKirjaus(props) {

    const history = useHistory();
    const { id } = useParams();

    const poistettavaKirjaus = props.kirjaukset.filter((kirjaus) => {
                                return (kirjaus.id === id);
                            })[0];

    const kasitteleLomake = (e) => {

        e.preventDefault();

        let kirjauksetApu = props.kirjaukset.filter((kirjaus) => {
            return (kirjaus.id !== id);
        });
    
        props.setKirjaukset([...kirjauksetApu]);
    
        history.push("/");

    }

    return (
        <form onSubmit={kasitteleLomake}>

        <Otsikko tyyli="pieni">Vahvista kirjaukset poisto</Otsikko>

        <Typography
            style={{marginBottom : 10}}
        >Haluatko varmasti poistaa kirjauksen {poistettavaKirjaus.nimi}  {poistettavaKirjaus.maara} €?</Typography>

        <Button 
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            style={{marginBottom : 10}}
        >Poista kirjaus</Button>

        <Button 
            component={Link}
            to="/"
            variant="contained"
            color="default"
            fullWidth
        >Peruuta</Button>

        </form>
    )
}

export default PoistaKirjaus;